@import './mixin.scss';

.basic-item{
  @include flex-col-start-start;
  &:hover{
    transition: .4s;
    transform: translateY(-15px);
  }

  .img-area{
    position: relative;
    min-height: 180px;
    img{
      border-radius: 8px;
      width: 100%;
    }
  }
  .info-area{
    width: 100%;
    .title-area{
      @include body02;
      @include text-ellipsis;
      margin-top: 6px
    }
  }
  &.popular{
    .num{
      @include title01;
      position: absolute;
      top: 0;
      padding: 0 10px;
      text-shadow: 1px 2px 10px #444;
      font-style: italic;
    }
  }
  &.normal{
    &:hover{
      .hover-detail{
        display: block;
      }
    }
  }
  .hover-detail{
    display: none;
    width: 100%;
    height: 100%;
    padding: 10px;
    box-sizing: border-box;
    background: rgba(0, 0, 0, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 8px;
    overflow: hidden;
    .title{
      @include body01;
      padding-bottom: 10px
    }
    .overview{
      @include body04;
      color: #ccc;
      overflow: hidden;
      text-overflow: ellipsis;
      word-wrap: break-word;
      display: -webkit-box;
      -webkit-line-clamp: 5; /* ellipsis line */
      -webkit-box-orient: vertical;
      line-height: 1.2em;
      height: auto;
    }
  }
}